.desktop{display: block;}
.mobile{display: none;}
.scroll-to-top{
  background-color: white;
  right: 40px;
  bottom: 35px!important;
  position: fixed;
  z-index: 5;
  cursor: pointer;
  border-radius: 7px;
  width: 40px;
  height: 40px;
  border-radius: 50%!important;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px!important;
 
  svg{
    width: 18px;
  }
}
#profile-bg{
 .width-container{
    width: 64%;
    margin: 0 auto;
  
  }
  .header{
    border-top: solid 7px #90a0fd;
    border-bottom: solid 1px #90a0fd;
    .contact-info {
      text-align: right; 
      padding: 10px 0px;
      margin: 0;
      list-style-type: none; 
      display: flex;
      align-items:center;
      justify-content: end;
      li{
        display: inline-block; 
        margin-left: 20px; 
        font-size: 14px;
        line-height: 18px;
        font-weight: 500;
        img{
          width: 26px;
          height: 26px;
          cursor: pointer;
        }
      }
    }
  }
  .content{
    padding: 20px 0px;
    .profile-flex{
      display: flex;
      justify-content: flex-start;
      align-items: center;
      .profile-img {
        padding: 20px 0px 0px 0px;
   img {
          width: 124px;
          height: 124px;
          border-radius: 50%;
          border: solid 2px #90a0fd46;
          cursor: pointer;
          animation: pulse-animation 3s infinite;
        }
      //   img:hover {
      //     width: 124px;
      //     height: 124px;
      //     border-radius: 50%;
      // transition:  0.5s linear all;
      //     border: solid 5px #90a0fd;
      //     box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
      //   }
    
      }

      
      
      @keyframes pulse-animation {
        0% {
          box-shadow: 0 0 0 0px rgba(10, 79, 241, 0.253);
        }
        100% {
          box-shadow: 0 0 0 20px rgba(15, 40, 231, 0.132);
        }
      }
      
    .profile-heading{
      font-size: 26px;
      line-height: 32px;
      font-weight: bold;
      color: black;
      padding: 0px 0px 0px 30px;
    }
    
  }
  .name{
    font-size: 24px;
    line-height: 30px;
    font-weight: 500;
    color: rgb(57, 57, 57);
    padding: 10px 0px;
  }
  .profile-desc{
    font-size: 14px;
    line-height: 22px;
    font-weight: normal;
    color: #000000;
    padding: 10px 0px;
  }
  .project-sec{
    padding: 20px 0px 10px 0px;
  .heading{
    font-size: 18px;
    line-height: 26px;
    font-weight: bold;
    padding: 60px 0px 5px 0px;
    border-bottom: solid 5px #90a0fd;
  }
  .project-item-flex{
    display: flex;
    justify-content: space-between;
    align-items: center;
    .project-name{
      width: 33.33%;
      font-size: 14px;
    line-height: 22px;
    font-weight: normal;
    color: #000000;
    padding: 10px 0px 10px 0px;
    cursor: pointer;
    text-decoration: underline;
    a{
      color: #000000;
    }
    }
  }
}
  }
  .footer{
    padding: 20px 0px;
    background-color: #90a0fd;
  }
}


//<---- mobile responsive code---->//
@media only screen and (min-width: 360px) and (max-width: 768px) {
  .desktop{display: none;}
  .mobile{display: block;}

  #profile-bg{
    .width-container{
       width: 90%;
       margin: 0 auto;
     
     }
     .header{
       .contact-info {
         padding: 10px 0px 0px 0px;
         flex-wrap: wrap;
         li{
           margin-bottom: 10px;
           img{
             width: 26px;
             height: 26px;
             cursor: pointer;
           }
         }
       }
     }
     .content{
       padding: 20px 0px;
       .profile-flex{
         flex-wrap: wrap;
         .profile-img {
           padding: 0px 0px 0px 0px;
      img {
             width: 118px;
             height: 118px;
           }
           img:hover {
             width: 118px;
             height: 118px;
           }
         
         }
        
       .profile-heading{
         font-size: 20px;
         line-height: 26px;
         font-weight: bold;
         color: black;
         padding: 0px 0px 0px 00px;
       }
       
     }
     .name{
       font-size: 20px;
       line-height: 24px;
       font-weight: 500;
       color: rgb(57, 57, 57);
       padding: 10px 0px;
     }
     .profile-desc{
       font-size: 14px;
       line-height: 22px;
       font-weight: normal;
       color: #000000;
       padding: 10px 0px;
     }
     .project-sec{
       padding: 10px 0px 10px 0px;
     .heading{
       font-size: 18px;
       line-height: 26px;
       font-weight: bold;
       padding: 20px 0px 5px 0px;
       border-bottom: solid 5px #90a0fd;
     }
     .project-item-flex{
       display: flex;
       justify-content: space-between;
       align-items: center;
       .project-name{
         width: 50%;
         font-size: 14px;
       line-height: 22px;
       font-weight: normal;
       color: #000000;
       padding: 5px 0px 5px 0px;
       cursor: pointer;
       text-decoration: underline;
       a{
         color: #000000;
       }
       }
     }
   }
     }
     .footer{
       padding: 20px 0px;
       background-color: #90a0fd;
     }
   }
   

}

